import Captcha from './Captcha.vue';
import toastr from 'toastr';
import TinyPagination from './pagging/opencps_pagination.vue';
toastr.options = {
  'closeButton': true,
  'timeOut': '3000'
};
export default {
  props: ['serviceCode'],
  components: {
    'captcha': Captcha,
    'tiny-pagination': TinyPagination
  },
  data: function data() {
    return {
      dialog_selectAgency: false,
      validFormSelectGov: false,
      govAgencyListTiepNhan: [],
      govAgencyTiepNhanSelected: '',
      loading: false,
      loaded: false,
      validFormSearch: false,
      validSearch: false,
      showFilter: false,
      capCoQuanThucHien: '',
      dialog_captcha: false,
      validCaptcha: false,
      govAgencyList: [],
      govAgencyFilter: '',
      domainList: [],
      domainListRender: [],
      domainFilter: '',
      serviceCodeFilter: '',
      administrationList: [],
      administrationFilter: '',
      leverFilter: '',
      panelAgency: [],
      panelDomain: [],
      govAgencies: [],
      govAgencyRender: [],
      serviceOptions: [],
      serviceOptionsSelect: '',
      serviceConfigSelect: '',
      serviceConfigList: [],
      serviceConfigListRender: [],
      serviceInfoIdSelect: '',
      serviceOptionsProcess: [],
      listQuanHuyen: [],
      quanHuyenSelect: '',
      listLerver: [],
      selectOption: false,
      govAgencyCodeSelect: '',
      serviceNameKey: '',
      activeFilterKey: false,
      loadingMutiple: true,
      dataPostDossier: '',
      serviceLastest: false,
      numberPerPage: 10,
      agencyPage: 1,
      serviceTotal: 0,
      serviceLastestTotal: 0,
      serviceLastestPage: 1,
      dialogVerifycation: false,
      verificationApplicantCreateDossier: false,
      hasVerify: false,
      dialogLoadingCreate: true,
      dialog_selectOption: true,
      serviceInfoLastestList: []
    };
  },
  computed: {
    currentIndex: function currentIndex() {
      return this.$store.getters.index;
    },
    currentUser: function currentUser() {
      return this.$store.getters.loadingInitData.user;
    },
    userLoginInfomation: function userLoginInfomation() {
      return this.$store.getters.getUserLogin;
    },
    isMobile: function isMobile() {
      return this.$store.getters.getIsMobile;
    }
  },
  created: function created() {
    var vm = this; //

    try {
      vm.verificationApplicantCreateDossier = hasVerificationCreateDossier;
    } catch (error) {} // 


    var current = vm.$router.history.current;
    var newQuery = current.query;
    vm.govAgencyFilter = newQuery['agency'] ? newQuery['agency'] : '';
    vm.capCoQuanThucHien = newQuery['administration'] ? newQuery['administration'] : '';
    vm.domainFilter = newQuery['domain'] ? newQuery['domain'] : '';
    vm.leverFilter = newQuery['lever'] ? Number(newQuery['lever']) : '';
    vm.serviceCodeFilter = newQuery['serviceCode'] ? newQuery['serviceCode'] : '';
    vm.serviceLastest = String(newQuery['lastest']) === 'true' ? true : false;

    if (vm.govAgencyFilter || vm.serviceLastest) {
      vm.filterAndSort();
    }

    if (String(vm.serviceCode) !== '0') {
      var params = {
        service: vm.serviceCode
      };
      vm.$store.dispatch('getServiceConfigs', params).then(function (res) {
        vm.pullServiceOptions(res.data[0]);
      });
    }

    vm.getServiceAdminisTration();
    vm.getLevers();

    if (vm.capCoQuanThucHien) {
      vm.getAgencys(vm.capCoQuanThucHien);
    }

    if (vm.govAgencyFilter) {
      vm.getDomains();
    }
  },
  updated: function updated() {},
  watch: {
    '$route': function $route(newRoute, oldRoute) {
      var vm = this;
      var newQuery = newRoute.query;
      vm.govAgencyFilter = newQuery['agency'] ? newQuery['agency'] : '';
      vm.capCoQuanThucHien = newQuery['administration'] ? newQuery['administration'] : '';
      vm.domainFilter = newQuery['domain'] ? newQuery['domain'] : '';
      vm.leverFilter = newQuery['lever'] ? Number(newQuery['lever']) : '';
      vm.serviceCodeFilter = newQuery['serviceCode'] ? newQuery['serviceCode'] : '';
      vm.serviceLastest = String(newQuery['lastest']) === 'true' ? true : false;

      if (vm.govAgencyFilter || vm.serviceLastest) {
        vm.filterAndSort();
      }
    },
    serviceLastest: function serviceLastest(val) {
      var vm = this;

      if (val) {
        vm.govAgencyFilter = '';
        vm.domainFilter = '';
        vm.serviceNameKey = '';
        vm.filterService();
      } else {
        vm.filterService();
      }
    },
    capCoQuanThucHien: function capCoQuanThucHien(val) {
      var vm = this;

      if (val) {
        vm.getAgencys(val);

        if (val === 'XA_PHUONG') {
          vm.getDictcollectionsQuanHuyen();
        }
      }
    },
    quanHuyenSelect: function quanHuyenSelect(val) {
      var vm = this;

      if (val) {
        vm.getAgencys(vm.capCoQuanThucHien, val);
      } else {
        vm.getAgencys(vm.capCoQuanThucHien);
      }
    },
    govAgencyFilter: function govAgencyFilter(val) {
      var vm = this;

      if (val) {
        vm.getDomains(val);
      }
    }
  },
  methods: {
    changePage: function changePage(config) {
      var vm = this;
      var current = vm.$router.history.current;
      var newQuery = current.query;
      var queryString = '?';
      vm.agencyPage = config.page;
      newQuery['page'] = '';

      for (var key in newQuery) {
        if (newQuery[key] !== '' && newQuery[key] !== 'undefined' && newQuery[key] !== undefined && newQuery[key] !== null && newQuery[key] !== 'null') {
          queryString += key + '=' + newQuery[key] + '&';
        }
      }

      queryString += 'page=' + vm.agencyPage;
      vm.$router.push({
        path: current.path + queryString,
        query: {
          renew: Math.floor(Math.random() * (100 - 1 + 1)) + 1
        }
      });
    },
    changePageServiceLastest: function changePageServiceLastest(config) {
      var vm = this;
      var current = vm.$router.history.current;
      var newQuery = current.query;
      var queryString = '?';
      vm.serviceLastestPage = config.page;
      newQuery['page'] = '';

      for (var key in newQuery) {
        if (newQuery[key] !== '' && newQuery[key] !== 'undefined' && newQuery[key] !== undefined && newQuery[key] !== null && newQuery[key] !== 'null') {
          queryString += key + '=' + newQuery[key] + '&';
        }
      }

      queryString += 'page=' + vm.agencyPage;
      vm.$router.push({
        path: current.path + queryString,
        query: {
          renew: Math.floor(Math.random() * (100 - 1 + 1)) + 1
        }
      });
    },
    filterService: function filterService() {
      var vm = this;
      var current = vm.$router.history.current;
      var newQuery = current.query;

      if (vm.$refs.form.validate() || vm.serviceLastest) {
        vm.validSearch = false;
        setTimeout(function () {
          var queryString = '?';
          newQuery['administration'] = vm.capCoQuanThucHien;
          newQuery['agency'] = vm.govAgencyFilter;
          newQuery['domain'] = vm.domainFilter;
          newQuery['serviceCode'] = vm.serviceCodeFilter;
          newQuery['lever'] = vm.leverFilter;
          newQuery['page'] = 1;

          if (vm.serviceLastest) {
            newQuery['lastest'] = vm.serviceLastest;
          } else {
            newQuery['lastest'] = '';
          }

          newQuery['keyword'] = String(vm.serviceNameKey).trim();

          for (var key in newQuery) {
            if (newQuery[key] !== '' && newQuery[key] !== 'undefined' && newQuery[key] !== undefined && newQuery[key] !== null) {
              queryString += key + '=' + newQuery[key] + '&';
            }
          }

          vm.$router.push({
            path: current.path + queryString,
            query: {
              renew: Math.floor(Math.random() * (100 - 1 + 1)) + 1
            }
          });
        }, 10);
      } else {
        vm.validSearch = true;

        if (vm.serviceLastest) {
          newQuery['lastest'] = vm.serviceLastest;
        } else {
          newQuery['lastest'] = '';
        }
      }
    },
    filterAndSort: function filterAndSort() {
      var vm = this;
      vm.loading = true;
      vm.loaded = true;
      var current = vm.$router.history.current;
      var newQuery = current.query;

      if (!newQuery.hasOwnProperty('lastest')) {
        vm.agencyPage = newQuery.hasOwnProperty('page') && newQuery['page'] ? Number(newQuery['page']) : 1;
        var params = {
          start: vm.agencyPage * vm.numberPerPage - vm.numberPerPage,
          end: vm.agencyPage * vm.numberPerPage,
          level: newQuery.hasOwnProperty('lever') && newQuery.lever ? newQuery.lever : '',
          agency: newQuery.hasOwnProperty('agency') && newQuery.agency ? newQuery.agency : '',
          domain: newQuery.hasOwnProperty('domain') && newQuery.domain ? newQuery.domain : '',
          keyword: newQuery.hasOwnProperty('keyword') && newQuery.keyword ? newQuery.keyword : '',
          serviceCode: newQuery.hasOwnProperty('serviceCode') && newQuery.serviceCode ? newQuery.serviceCode : ''
        };
        vm.$store.dispatch('getServiceConfigs', params).then(function (res) {
          vm.loading = false;
          vm.serviceTotal = res.total;

          if (res.data) {
            vm.serviceConfigListRender = res.data;
          } else {
            vm.serviceConfigListRender = [];
          }
        }).catch(function () {
          vm.loading = false;
          vm.serviceConfigListRender = [];
        });

        if (newQuery.hasOwnProperty('agency') && newQuery.agency) {
          vm.domainListTemp = vm.domainList.filter(function (e) {
            return e.govAgencyCode === newQuery.agency;
          });
        } else {
          vm.domainListTemp = vm.domainList;
        }
      }

      if (newQuery.hasOwnProperty('lastest') && newQuery.lastest && String(newQuery.lastest) !== 'false') {
        vm.serviceLastestPage = newQuery.hasOwnProperty('page') && newQuery['page'] ? Number(newQuery['page']) : 1;
        var _params = {
          start: vm.serviceLastestPage * vm.numberPerPage - vm.numberPerPage,
          end: vm.serviceLastestPage * vm.numberPerPage
        };
        vm.$store.dispatch('getServiceRecently', _params).then(function (result) {
          vm.loading = false;
          vm.serviceLastestTotal = result.total;
          vm.serviceInfoLastestList = result.data;
        }).catch(function () {
          vm.loading = false;
          vm.serviceInfoLastestList = [];
          vm.serviceLastestTotal = 0;
        });
      }
    },
    pullServiceOptions: function pullServiceOptions(item, serviceInfo) {
      var vm = this;

      if (vm.verificationApplicantCreateDossier && vm.userLoginInfomation && vm.userLoginInfomation['verification'] && String(vm.userLoginInfomation['verification']) === '2') {
        vm.hasVerify = true;
        setTimeout(function () {
          vm.dialogVerifycation = true;
        }, 300);
      } else {
        vm.serviceConfigSelect = item;
        vm.govAgencyCodeSelect = item.govAgencyCode;
        vm.serviceInfoIdSelect = serviceInfo ? serviceInfo.serviceInfoId : item.serviceInfoId;
        vm.$store.dispatch('getServiceOpionByProcess', item).then(function (result) {
          if (result) {
            vm.serviceOptions = result;

            if (String(vm.serviceCode) === '0') {
              vm.selectOption = false;

              if (result.length === 1) {
                vm.$store.dispatch('getServiceInfo', {
                  serviceInfoId: vm.serviceInfoIdSelect
                }).then(function (resServiceInfo) {
                  var data = {
                    serviceCode: resServiceInfo.serviceCode,
                    govAgencyCode: item.govAgencyCode,
                    templateNo: result[0].templateNo,
                    originality: vm.getOriginality(),
                    j_captcha_response: ''
                  };

                  if (!vm.isOffLine) {
                    vm.trackingBTTT(resServiceInfo.serviceCode);
                    vm.$store.dispatch('postDossier', data).then(function (result) {
                      vm.loadingAction = false;
                      vm.indexAction = -1;
                      vm.$router.push({
                        path: '/danh-sach-ho-so/' + 0 + '/ho-so/' + result.dossierId + '/NEW',
                        query: vm.$router.history.current.query
                      });
                    });
                  } else {
                    vm.dataPostDossier = data;
                    vm.$refs.captcha.makeImageCap();
                    vm.dialog_captcha = true;
                  }
                });
              }
            } else {
              if (result.length === 1) {
                vm.$store.dispatch('getServiceInfo', {
                  serviceInfoId: vm.serviceInfoIdSelect
                }).then(function (resServiceInfo) {
                  var data = {
                    serviceCode: resServiceInfo.serviceCode,
                    govAgencyCode: item.govAgencyCode,
                    templateNo: result[0].templateNo,
                    originality: vm.getOriginality(),
                    j_captcha_response: ''
                  };

                  if (!vm.isOffLine) {
                    vm.trackingBTTT(resServiceInfo.serviceCode);
                    vm.$store.dispatch('postDossier', data).then(function (result) {
                      vm.loadingAction = false;
                      vm.indexAction = -1;
                      vm.$router.push({
                        path: '/danh-sach-ho-so/' + 0 + '/ho-so/' + result.dossierId + '/NEW',
                        query: vm.$router.history.current.query
                      });
                    });
                  } else {
                    vm.dataPostDossier = data;
                    vm.$refs.captcha.makeImageCap();
                    vm.dialog_captcha = true;
                  }
                });
              } else {
                vm.serviceOptionsProcess = result;
                vm.selectOption = true;
              }
            }
          } else {
            vm.$store.dispatch('getServiceConfigDetail', item).then(function (result) {
              if (result.hasOwnProperty('serviceUrl') && result.serviceUrl) {
                window.location.href = result.serviceUrl;
              }
            }).catch(function () {});
          }
        }).catch(function (result) {
          vm.serviceOptions = [];
        });
      }
    },
    selectServiceOption: function selectServiceOption(item, itemServiceConfig) {
      var vm = this;
      vm.serviceConfigSelect = itemServiceConfig;
      vm.govAgencyCodeSelect = itemServiceConfig.govAgencyCode;
      vm.serviceInfoIdSelect = itemServiceConfig.serviceInfoId;
      vm.loadingMutiple = true;
      vm.$store.dispatch('getServiceInfo', {
        serviceInfoId: vm.serviceConfigSelect.serviceInfoId
      }).then(function (resServiceInfo) {
        var data = {
          serviceCode: resServiceInfo.serviceCode,
          govAgencyCode: itemServiceConfig.govAgencyCode,
          templateNo: item.templateNo,
          originality: vm.getOriginality(),
          j_captcha_response: ''
        };

        if (!vm.isOffLine) {
          vm.trackingBTTT(resServiceInfo.serviceCode);
          vm.$store.dispatch('postDossier', data).then(function (result) {
            vm.loadingAction = false;
            vm.indexAction = -1;
            vm.$router.push({
              path: '/danh-sach-ho-so/' + 0 + '/ho-so/' + result.dossierId + '/NEW',
              query: vm.$router.history.current.query
            });
          });
        } else {
          vm.dataPostDossier = data;
          vm.$refs.captcha.makeImageCap();
          vm.dialog_captcha = true;
        }
      });
    },
    pullServiceOptionsLastest: function pullServiceOptionsLastest(item) {
      var vm = this;
      vm.$store.dispatch('getServiceOpionByProcess', item).then(function (result) {
        if (result) {
          vm.serviceOptions = result;
        }
      });
    },
    doCreateDossier: function doCreateDossier() {
      var vm = this;
      vm.$refs.formCaptcha.resetValidation();
      var captchaInput = vm.$refs.captcha.j_captcha_response;

      if (vm.$refs.formCaptcha.validate()) {
        vm.dataPostDossier['j_captcha_response'] = captchaInput;
        vm.createDossier(vm.dataPostDossier);
      }
    },
    createDossier: function createDossier(data) {
      var vm = this;

      if (vm.serviceConfigSelect.serviceUrl) {
        window.location.href = vm.serviceConfigSelect;
      } else {
        vm.trackingBTTT(data.serviceCode);
        vm.$store.dispatch('postDossier', data).then(function (result) {
          if (result['status'] !== undefined && result['status'] === 203) {
            vm.loadingAction = false;
            toastr.clear();
            toastr.error('Mã captcha không chính xác. Vui lòng thử lại');
            vm.$refs.captcha.makeImageCap();
          } else {
            vm.loadingAction = false;
            vm.dialog_captcha = false;
            vm.indexAction = -1;
            vm.$router.push({
              path: '/danh-sach-ho-so/' + 0 + '/ho-so/' + result.data.dossierId + '/NEW',
              query: vm.$router.history.current.query
            });
          }
        }).catch(function (reject) {
          toastr.error('Nộp hồ sơ không thành công');
          vm.$refs.captcha.makeImageCap();
        });
      }
    },
    serviceConfigs: function serviceConfigs(config) {
      if (Array.isArray(config)) {
        return config;
      } else {
        if (config.hasOwnProperty('serviceConfigId')) {
          return [config];
        } else {
          return [];
        }
      }
    },
    getColor: function getColor(level) {
      if (level === 2) {
        return 'green';
      } else if (level === 3) {
        return 'orange darken-1';
      } else if (level === 4) {
        return 'red';
      }
    },
    goBack: function goBack() {
      window.history.back();
    },
    getServiceAdminisTration: function getServiceAdminisTration() {
      var vm = this;
      var current = vm.$router.history.current;
      var newQuery = current.query;

      var sortCode = function sortCode(serviceList) {
        function compare(a, b) {
          if (a.dictGroupId < b.dictGroupId) return -1;
          if (a.dictGroupId > b.dictGroupId) return 1;
          return 0;
        }

        return serviceList.sort(compare);
      };

      vm.$store.dispatch('getServiceAdminisTration', {}).then(function (res) {
        vm.administrationList = sortCode(res);
        vm.capCoQuanThucHien = newQuery['administration'] ? newQuery['administration'] : res[0]['groupCode'];
        vm.getAgencys(vm.capCoQuanThucHien);
      }).catch(function () {});
    },
    showSelectGov: function showSelectGov(govList, serviceInfo) {
      var vm = this;
      vm.serviceInfoIdSelect = serviceInfo.serviceInfoId;
      vm.govAgencyTiepNhanSelected = '';
      vm.serviceOptionsSelect = '';
      vm.govAgencyListTiepNhan = vm.serviceConfigs(govList);
      vm.dialog_selectAgency = true;
    },
    changeGovSelected: function changeGovSelected() {
      var vm = this;
      setTimeout(function () {
        vm.pullServiceOptionsLastest(vm.govAgencyTiepNhanSelected);
      }, 100);
    },
    submitSelectGov: function submitSelectGov() {
      var vm = this;

      if (vm.$refs.formSelect.validate()) {
        vm.govAgencyTiepNhanSelected.serviceInfoId = vm.serviceInfoIdSelect;

        if (vm.serviceOptions.length === 1) {
          vm.selectServiceOption(vm.serviceOptions[0], vm.govAgencyTiepNhanSelected);
        } else {
          vm.selectServiceOption(vm.serviceOptionsSelect, vm.govAgencyTiepNhanSelected);
        }
      }
    },
    getDictcollectionsQuanHuyen: function getDictcollectionsQuanHuyen() {
      var vm = this;
      var filter = {
        groupCode: 'QUAN_HUYEN'
      };
      vm.$store.dispatch('getDictcollections', filter).then(function (res) {
        vm.listQuanHuyen = res.data;
      }).catch(function () {});
    },
    getLevers: function getLevers() {
      var vm = this;
      vm.$store.dispatch('getLevels').then(function (res) {
        vm.listLerver = res;
      }).catch(function () {});
    },
    getAgencys: function getAgencys(administrationCode, parentFilter) {
      var vm = this;
      var current = vm.$router.history.current;
      var newQuery = current.query;
      var data = {
        administration: administrationCode ? administrationCode : '',
        parent: parentFilter ? parentFilter : ''
      };

      if (!parentFilter) {
        vm.$store.dispatch('getAgencys', data).then(function (res) {
          vm.govAgencyList = res;
        }).catch(function () {
          vm.govAgencyList = [];
        });
      } else {
        vm.$store.dispatch('getAgencysFromParent', data).then(function (res) {
          vm.govAgencyList = res;
        }).catch(function () {
          vm.govAgencyList = [];
        });
      }
    },
    getDomains: function getDomains(agencyCode) {
      var vm = this;
      var data = {
        agency: agencyCode ? agencyCode : ''
      };
      vm.$store.dispatch('getDomains', data).then(function (res) {
        vm.domainList = res;
      }).catch(function () {
        vm.domainList = [];
      });
    },
    closeSelectOption: function closeSelectOption() {
      var vm = this;
      vm.dialog_selectOption = false; // vm.filterAndSort()
    },
    selectServiceOptionCRD: function selectServiceOptionCRD(item, govAgencyCode) {
      var vm = this;
      vm.$store.dispatch('getServiceInfo', {
        serviceInfoId: vm.serviceInfoIdSelect
      }).then(function (resServiceInfo) {
        vm.trackingBTTT(resServiceInfo.serviceCode);
        var data = {
          serviceCode: resServiceInfo.serviceCode,
          govAgencyCode: govAgencyCode,
          templateNo: item.templateNo,
          originality: vm.getOriginality()
        };
        vm.$store.dispatch('postDossier', data).then(function (result) {
          vm.loadingAction = false;
          vm.indexAction = -1;
          vm.$router.push({
            path: '/danh-sach-ho-so/' + 0 + '/ho-so/' + result.dossierId + '/NEW',
            query: vm.$router.history.current.query
          });
        });
      });
    },
    trackingBTTT: function trackingBTTT(serviceCode) {
      try {
        if (_govaq) {
          _govaq.push(['trackDVC', serviceCode, '1', '']);
        }
      } catch (error) {}
    }
  }
};